<template>
  <div class="data-table-wrapper">
    <table
      class="page-datatable"
      v-if="dataList.length != 0"
    >
      <thead>
        <tr>
          <th>
            <span>Projectnaam</span>
            <base-icon
              iconRef="chevron-down"
              position="right"
            />
          </th>
          <th>
            <span>Adres</span>
            <base-icon
              iconRef="chevron-down"
              position="right"
            />
          </th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="dataEntry in dataList"
          :key="dataEntry.id"
          @click="$emit('click', dataEntry.id)"
        >
          <td>
            <span>{{dataEntry.data().projectName}}</span>
          </td>
          <td>
            <span>{{dataEntry.data().address.city + ", " + dataEntry.data().address.street + " " + dataEntry.data().address.houseNumber + ", " + dataEntry.data().address.postCode}}</span>
          </td>
        </tr>
      </tbody>
    </table>
    <p
      class="page-datatable-disclaimer"
      v-if="dataList.length == 0"
    >Er zijn geen projecten gevonden.</p>
  </div>
</template>

<script>
export default {
  name: "projects-data-list",
  props: {
    dataList: {
      type: Array
    }
  }
};
</script>

<style>
</style>



