<template>
  <div>
    <loading-spinner v-if="loading"></loading-spinner>
    <div class="page-container" v-else>
      <client-data />
      <div class="page-box">
        <div class="page-box-title">
          <h3>Projecten</h3>
          <div class="flex-divider"></div>
          <base-button @click="openCreateModal" class="small-on-mobile">
            <base-icon iconRef="plus" position="left" />
            <span class="button-label">Nieuw Project</span>
          </base-button>
        </div>
        <div class="page-box-datatable-container">
          <projects-data-list
            :dataList="currentClientProjectsList"
            @click="dataEntryClick($event)"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import clientData from "./client-page-components/client-data";
import projectsDataList from "./data-list-components/projects-data-list.vue";
import LoadingSpinner from "./LoaderSpinner.vue";

import Firebase from "firebase";

export default {
  name: "Client",
  components: {
    clientData,
    projectsDataList,
    LoadingSpinner
  },
  data() {
    return {
      loading: true,
      currentClient: null,
      currentClientProjectsList: []
    };
  },
  methods: {
    openCreateModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "project",
        modalType: "create",
        shown: true
      });
    },
    dataEntryClick: function(value) {
      this.openProject(value);
    },
    openProject: function(targetProjectId) {
      this.$router.push(`/project/${targetProjectId}`);
    }
  },
  watch: {
    currentClient: function() {
      this.$store.commit("setCurrentClientPageClient", this.currentClient);
    }
  },
  mounted() {
    Firebase.firestore()
      .collection("clients")
      .doc(this.$route.params.clientId)
      .get()
      .then(doc => {
        this.currentClient = doc;
        this.$store.commit("setCurrentClientPage", {
          client: this.currentClient
        });
        this.$setPageTitle(`Klant - ${this.currentClient.data().clientName}`);
        return doc;
      })
      .then(doc => {
        Firebase.firestore()
          .collection("projects")
          .where("belongsTo", "==", doc.id)
          .where("trashed", "==", false)
          .get()
          .then(snapshot => {
            snapshot.forEach(doc => {
              this.currentClientProjectsList.push(doc);
            });
            this.loading = false;
          });
      });
  },
  destroyed() {
    this.$store.commit("setCurrentClientPage", {});
  }
};
</script>

<style>
</style>



